import request from '@/plugin/axios'

/**
 * https://www.jianshu.com/p/e6f072839282
 *  openAPI  (https://www.oschina.net/openapi/docs)
 */
// 
export function getRandomJoke () {
  return request({
    url: `https://www.apiopen.top/satinGodApi?type=1&page=1`,
    method: 'get'
  })
}

// 
export function getRealTimeJoke () {
  return request({
    url: `https://api.apiopen.top/getJoke?page=1&count=2&type=video`,
    method: 'get'
  })
}

// id
export function getSingleJoke ({param}) {
  return request({
    url: `https://api.apiopen.top/getSingleJoke?sid=${param.sid}`,
    method: 'get'
  })
}

// 
export function getDailyLook () {
  return request({
    url: `https://meiriyikan.cn/api/json.php`,
    method: 'get'
  })
}
// 
export function getNews () {
  return request({
    url: `https://www.apiopen.top/journalismApi`,
    method: 'get'
  })
}

//  
export function getNovelSearch ({ param }) {
  return request({
    url: `https://www.apiopen.top/novelSearchApi?name=${param.name}`,
    method: 'get'
  })
}
// 
export function getNovelDetail ({ param }) {
  return request({
    url: `https://www.apiopen.top/novelInfoApi?name=${param.name}`,
    method: 'get'
  })
}
